.container {
    padding: 0;
    margin-right: auto;
    margin-left: auto;
}

@media screen and (min-width: 320px) {
    .container {
        width: calc(100% - 40px);
    }
}

@media screen and (min-width: 480px) {
    .container {
        width: 440px;
    }
}

@media screen and (min-width: 768px) {
    .container {
        width: 704px;
    }
}

@media screen and (min-width: 1200px) {
    .container {
        width: 1136px;
    }
}